// hooks/useLeaderboard.ts
import { useState, useCallback } from 'react';
import { 
  collection, 
  addDoc, 
  query, 
  orderBy, 
  limit, 
  getDocs,
  where,
  Timestamp
} from 'firebase/firestore';
import { db } from '@/config/firebase';

interface LeaderboardEntry {
  id: string;
  userId: string;
  displayName: string;
  puzzleDate: string;
  moves: number;
  hintsUsed: number;
  totalTime: number;
  completedAt: Date;
  score: number;
}

export const useLeaderboard = () => {
  const [currentLeaderboard, setCurrentLeaderboard] = useState<LeaderboardEntry[]>([]);
  const [currentDate, setCurrentDate] = useState<string>('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const fetchLeaderboardForDate = useCallback(async (date: string) => {
    console.log('fetchLeaderboardForDate called with:', JSON.stringify(date));
    console.log('Comparing to stored value "2025-10-13":', date === "2025-10-13");
    
    if (!date || typeof date !== 'string' || date.trim() === '') {
      setError('Invalid puzzle date');
      return;
    }
    
    setLoading(true);
    setError(null);
    
    try {
      const q = query(
        collection(db, 'nfl-connections-leaderboard'),
        where('puzzleDate', '==', date),
        orderBy('totalTime', 'asc'),
        limit(20)
      );

      console.log('Query created for date:', date);
      
      const snapshot = await getDocs(q);

      console.log('Snapshot size:', snapshot.size);
      console.log('Snapshot docs:', snapshot.docs.map(d => d.data()));
      
      const entries: LeaderboardEntry[] = [];
      snapshot.forEach((doc) => {
        const data = doc.data();
        entries.push({
          id: doc.id,
          ...data,
          completedAt: data.completedAt?.toDate() || new Date()
        } as LeaderboardEntry);
      });
      
      setCurrentLeaderboard(entries);
      setCurrentDate(date);
      
      // Dispatch event with leaderboard data
      window.dispatchEvent(
        new CustomEvent('widgetLeaderboardData', {
          detail: {
            entries,
            currentDate: date
          }
        })
      );
      
    } catch (error: any) {
      console.error('Leaderboard fetch error:', error);
      setError('Failed to load leaderboard');
      setCurrentLeaderboard([]);
    } finally {
      setLoading(false);
    }
  }, []);

  const submitScore = async (
    userId: string,
    displayName: string,
    gameStats: { moves: number; hintsUsed: number; totalTime: number; score: number },
    puzzleDate: string
  ) => {
    try {
      setError(null);
      
      // Check if user already has a score for this puzzle date
      const existingScoreQuery = query(
        collection(db, 'nfl-connections-leaderboard'),
        where('userId', '==', userId),
        where('puzzleDate', '==', puzzleDate),
        limit(1)
      );
      
      const existingScoreSnapshot = await getDocs(existingScoreQuery);
      
      if (!existingScoreSnapshot.empty) {
        console.log('Score already submitted for this date');
        return;
      }
      
      const leaderboardEntry = {
        userId,
        displayName,
        puzzleDate,
        moves: gameStats.moves,
        hintsUsed: gameStats.hintsUsed,
        totalTime: gameStats.totalTime,
        score: gameStats.score,
        completedAt: Timestamp.now(),
      };
      
      await addDoc(collection(db, 'nfl-connections-leaderboard'), leaderboardEntry);
      
      // Refresh leaderboard after submission
      await fetchLeaderboardForDate(puzzleDate);
      
    } catch (error: any) {
      console.error('Score submission error:', error);
      setError('Failed to submit score');
    }
  };

  const getUserRank = async (userId: string, puzzleDate: string): Promise<{ rank: number; userEntry: LeaderboardEntry } | null> => {
    try {
      const q = query(
        collection(db, 'nfl-connections-leaderboard'),
        where('puzzleDate', '==', puzzleDate),
        orderBy('totalTime', 'asc')
      );
      
      const snapshot = await getDocs(q);
      const allEntries: LeaderboardEntry[] = [];
      
      snapshot.forEach((doc) => {
        const data = doc.data();
        allEntries.push({
          id: doc.id,
          ...data,
          completedAt: data.completedAt?.toDate() || new Date()
        } as LeaderboardEntry);
      });
      
      const userEntryIndex = allEntries.findIndex(entry => entry.userId === userId);
      if (userEntryIndex === -1) return null;
      
      return {
        rank: userEntryIndex + 1,
        userEntry: allEntries[userEntryIndex]
      };
    } catch (error) {
      console.error('Get user rank error:', error);
      return null;
    }
  };

  return {
    currentLeaderboard,
    currentDate,
    loading,
    error,
    submitScore,
    fetchLeaderboardForDate,
    getUserRank
  };
};
